import { Button, Empty, Tabs } from "@taroify/core"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function PresetEmpty() {
  const [tab, setTab] = useState(0)
  return (
    <Block title="">
      <Tabs value={tab} onChange={setTab}>
        <Tabs.TabPane title="">
          <Empty>
            <Empty.Image src="error" />
            <Empty.Description></Empty.Description>
          </Empty>
        </Tabs.TabPane>
        <Tabs.TabPane title="">
          <Empty>
            <Empty.Image src="network" />
            <Empty.Description></Empty.Description>
          </Empty>
        </Tabs.TabPane>
        <Tabs.TabPane title="">
          <Empty>
            <Empty.Image src="search" />
            <Empty.Description></Empty.Description>
          </Empty>
        </Tabs.TabPane>
      </Tabs>
    </Block>
  )
}

export default function EmptyDemo() {
  return (
    <Page title="Empty " className="empty-demo">
      <Block title="">
        <Empty>
          <Empty.Image />
          <Empty.Description></Empty.Description>
        </Empty>
      </Block>
      <PresetEmpty />
      <Block title="">
        <Empty>
          <Empty.Image
            className="custom-empty__image"
            src="https://img.yzcdn.cn/vant/custom-empty-image.png"
          />
          <Empty.Description></Empty.Description>
        </Empty>
      </Block>
      <Block title="">
        <Empty>
          <Empty.Image />
          <Empty.Description></Empty.Description>
          <Button shape="round" color="danger" className="bottom-button">
            
          </Button>
        </Empty>
      </Block>
    </Page>
  )
}
